import React, { Component } from "react";
import All from "./all";
import Completed from "./completed"
import Incompleted from "./incompleted"

export class comp1 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [
        {
          text: "Task 1",
          key: "1",
          comp: false,
        },
        {
          text: "Task 2",
          key: "2",
          comp: true,
        },
      ],
      currentItem: {
        text: "",
        key: "",
        comp: false,
      },
      count: 0,
    };
    this.handleInput = this.handleInput.bind(this);
    this.addItem = this.addItem.bind(this);
    this.checkBoxChange = this.checkBoxChange.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
  }

  counts() {
    let countt = 0;

    const updatedCount = this.state.items.map((item) => {
      if (item.comp === true) {
        countt = countt + 1;
      }
      console.log(countt);
    });
    return this.setState({ count: countt });
  }

  handleInput(e) {
    this.setState({
      currentItem: {
        text: e.target.value,
        key: Date.now(),
        comp: false,
      },
    });
  }

  addItem(e) {
    e.preventDefault();
    const newItem = this.state.currentItem;
    if (newItem.text !== "") {
      const Items = [...this.state.items, newItem];
      this.setState(
        {
          items: Items,
          currentItem: {
            text: "",
            key: "",
            comp: false,
          },
        },
        () => {
          console.log(Items);
        }
      );
    }
  }

  checkBoxChange(key) {
    this.setState(
      (prevState) => {
        const updatedItems = prevState.items.map((item) => {
          if (item.key === key) {
            return {
              ...item,
              comp: !item.comp,
            };
          }
          return item;
        });
        return {
          items: updatedItems,
        };
      } /*() => {
      this.counts();
    }*/
    );
  }

  deleteItem(key) {
    const filterItems = this.state.items.filter((item) => item.key !== key);
    this.setState(
      {
        items: filterItems,
      } /*() => {
      this.counts();
    }*/
    );
  }

  componentDidMount() {
    this.interval = setInterval(() => this.counts(), 100);
  }
  // componentWillUnmount() {
  //   clearInterval(this.interval);
  // }

  render() {
    return (
      <div className="container">
        <div className="head text-center">
          <form id="to-do-form" onSubmit={this.addItem}>
            <div className="input-group">
              <input
                className="form-control"
                type="text"
                placeholder="Enter the Task"
                value={this.state.currentItem.text}
                onChange={this.handleInput}
              />
              <div className="input-group-btn">
                <button className="btn btn-info" type="submit">
                  {" "}
                  Add{" "}
                </button>
              </div>
            </div>
          </form>
        </div>
        <div className="container content">
          <ul class="tabs nav nav-pills nav-justified">
            <li class="active">
              <a data-toggle="pill" href="#all">
                All <span class="badge badge-light">{this.state.items.length}</span>
              </a>
            </li>
            <li>
              <a data-toggle="pill" href="#completed">
                Completed <span class="badge badge-light">{this.state.count}</span>
              </a>
            </li>
            <li>
              <a data-toggle="pill" href="#incompleted">
                Incompleted <span class="badge badge-light">{this.state.items.length - this.state.count}</span>
              </a>
            </li>
          </ul>

          <div class="tab-content">
            <div id="all" class="tab-pane fade in active">
              <All
                items={this.state.items}
                checkBoxChange={this.checkBoxChange}
                deleteItem={this.deleteItem}
              />
            </div>
            <div id="completed" class="tab-pane fade">
              <Completed
                items={this.state.items}
                checkBoxChange={this.checkBoxChange}
                deleteItem={this.deleteItem}
              />
            </div>
            <div id="incompleted" class="tab-pane fade">
              <Incompleted
                items={this.state.items}
                checkBoxChange={this.checkBoxChange}
                deleteItem={this.deleteItem}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default comp1;
